import { Component, Input, OnInit } from '@angular/core';
import { Store } from "@ngxs/store";
import { SectionModel } from "../../../shared/models/section.model";
import { AddSectionAction, EditTextAction } from "../../../shared/store/config-tdn.action";
import { map } from "rxjs/operators";
import { FormBuilder, FormControl, FormGroup } from "@angular/forms";

@Component({
  selector: 'app-section',
  templateUrl: './section.component.html',
  styleUrls: ['./section.component.css']
})

export class SectionComponent implements OnInit {

  @Input() sectionName: string;

  currentSection: SectionModel;
  titleText: string;
  descriptionText: string ;
  buttonText: string;

  isTop: boolean;
  hover: boolean;
  private form: any;

  constructor(private store: Store, private _fb: FormBuilder) {
    this.currentSection = new SectionModel();
  }

  ngOnInit() {

    this.store.select(state => state.TdnSiteConfigStateStore.sections)
      .pipe(
        map((sections: SectionModel[]) => sections.find(s => s.sectionName === this.sectionName))
      )
      .subscribe((res: SectionModel) => {

        if (res) {
          this.currentSection = res;
        }

      });

    switch (this.sectionName) {
      case "Top" :
        this.titleText = "Le service de divertissement tout-en-un.\n Accessible  tout moment, sur tous vos crans.";
        this.descriptionText = "Accdez en illimit  tous vos contenus: films, musique, ebooks, jeux et logiciels.";
        this.buttonText = "Crez votre compte";
        this.isTop = true;
        break;
      case "Films" :
        this.titleText = "Film en illimits !";
        this.descriptionText = "Cliquez et regardez, c'est instantan ! Profitez d'un accs illimit  la totalit de notre catalogue streaming et en qualit HD. Des films les plus prims et rcents aux grands classiques, vous trouverez tout ce que vous cherchez sur Fuuze.";
        this.buttonText = "Dcouvrir le catalogue";
        break;
      case "Sport" :
        this.titleText = "Le sport en illimit !";
        this.descriptionText = "Crez votre compte ds maintenant et suivez vos sports prfrs en direct. Nous vous proposons les principales ligues nationales et internationales. Vous avez rat un match ? Revivez les meilleurs moments de ce match en vido.";
        this.buttonText = "Regarder maintenant !";
        break;
      case "Musique" :
        this.titleText = "Musique en illimits !";
        this.descriptionText = "Ecoutez la musique que vous aimez, o que vous soyez. Chez vous, dans les transports, ou au bureau, votre musique ne vous quittera plus! Retrouvez vos artistes prfrs, revivez les concerts de vos groupes favoris et crez vos playlists !";
        this.buttonText = "Lancer l'coute";
        break;
      case "EBook" :
        this.titleText = "Ebooks illimits !";
        this.descriptionText = "Accdez  des milliers douvrages, et choisissez selon vos envies ! Redcouvrez les grands classiques de la littrature, apprenez-en plus sur les sujets qui vous passionnent, suivez vos hros dans leurs aventures Il y en a pour tous les gots !";
        this.buttonText = "Explorer notre bibliothque";
        break;
      case "Jeux" :
        this.titleText = "Jeux illimits !";
        this.descriptionText = "Amusez-vous avec tous les jeux de notre collection. Fuuze vous propose une slection de jeux d'arcade accessibles sur tous vos appareils, sans aucune installation requise ! Exercez votre cerveau avec nos jeux de rflexion, partez  l'aventure en dfendant la plante contre une invasion d 'aliens ou dtendez-vous avec des jeux plus lgers!";
        this.buttonText = "Accder  tous nos jeux";
        break;
      case "Software" :
        this.titleText = "Logiciels illimits !";
        this.descriptionText = "Tlchargez en un clic ! Nous avons slectionn les meilleurs logiciels pour vous: retouchez vos photos, communiquez avec vos proches , apprenez une nouvelle langue, optimisez les performances de votre ordinateur... Nous avons tout ce qu'il vous faut ! Installez-en autant que vous voulez, ils sont  vous.";
        this.buttonText = "Dcouvrir tout nos logiciels";
        break;
      case "Feature" :
        this.titleText = "L'exprience Fuuze. Partout,  tous les instants.";
        this.descriptionText = "Ecoutez la musique que vous aimez, o que vous soyez. Chez vous, dans les transports, ou au bureau, votre musique ne vous quittera plus!\n" +
          "Retrouvez vos artistes prfrs, revivez les concerts de vos groupes favoris et crez vos playlists !";
        this.buttonText = "C'est parti !";
        break;
    }

    this.form = new FormGroup({
      descriptionText : new FormControl(this.descriptionText),
      titleText: new FormControl(this.titleText),
      buttonText : new FormControl(this.buttonText)
    })

    const section = new SectionModel();
    section.sectionName = this.sectionName;
    this.store.dispatch(new AddSectionAction(section))
  }

  updateTextContent(element){
    console.log(this.form.value.titleText)
      switch(element){
        case "title" :
          if(this.form.value.titleText.length < 1) {
            this.ngOnInit();
            break;
          }
          this.store.dispatch(new EditTextAction(this.form.value.titleText , this.sectionName ,element))
          break;
        case "description" :
          if(this.form.value.descriptionText.length < 1) {
            this.ngOnInit();
            break;
          }
          this.store.dispatch(new EditTextAction(this.form.value.descriptionText , this.sectionName ,element))
          break;
        case "button" :
          if(this.form.value.buttonText.length < 1) {
            this.ngOnInit();
            break;
          }
          this.store.dispatch(new EditTextAction(this.form.value.buttonText , this.sectionName ,element))
          break;
      }
  }

}
